import React from 'react';
import './Header.css';
import{Navbar,Container,Nav} from 'react-bootstrap';
import img1 from '../images/git.jpg';
import img2 from '../images/instra.jpg';
import img3 from '../images/linkedin.png';
export default function Header() {
  return <div className='red sticky-top'>
      <Navbar bg="light" expand="lg">
  <Container>
    <Navbar.Brand href="#home">Port<span>Folio</span></Navbar.Brand>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="me-auto justify-content-end hlink">
        <Nav.Link className='hov col-2' href="#home">Home</Nav.Link>
        <Nav.Link className='hov col-2' href="#About">About</Nav.Link>
        <Nav.Link className='hov col-2' href="#cont">Contact</Nav.Link>
     </Nav>
    </Navbar.Collapse>
    <Nav.Link href="https://github.com/Virendrawebdev" target={'_blank'}><img src={img1} alt='git'/></Nav.Link>
    <Nav.Link href="https://www.instragram.com"><img src={img2} alt='git'/></Nav.Link>
    <Nav.Link href="#"><img id='img3' src={img3} alt='git'/></Nav.Link>
  </Container>
  </Navbar>
  </div>;
}
