import React from 'react';
import './Contact.css';
import { Button } from 'reactstrap';
import img4 from '../images/envelope.jpg';

export default function Contact() {
  return (
    <div className='contact' id='cont'>
    <h1>Contact</h1>
    <div className='contactinfo'>
      
         <div className="ENV">
            <img src={img4}/>
         </div>
        <div className="name"><h1>NAME:</h1>
            <input type="text" />
        </div>
        <div className="name"><h1>EMAIL:</h1>
            <input type="email" name="email" id="id" />
        </div>
        <div className="name"><h1>NUMBER:</h1>
            <input type="number" />
        </div>
        <div className="name"><h1>MESSAGE:</h1>
            <textarea name="textme" id="" cols="23" rows="7"></textarea>
        </div>
        <Button color='success'  className="btn">SUBMIT</Button>
    </div>
    </div>
  )
}
